import React, { useState } from "react";
import { AiOutlinePlus } from "react-icons/ai";
import { BsFillTrashFill } from "react-icons/bs";
import { AiFillEdit } from "react-icons/ai";
import "./styles.css";

export default function App() {
  const [input, setInput] = useState("");
  const [items, setItems] = useState([]);
  const [toggle, setToggle] = useState(true);
  const [editItemIndex, setEditItemIndex] = useState(null);

  const addItems = () => {
    setItems([...items, input]);
    setInput("");
  };

  const deleteItems = (id) => {
    const delValue = items.filter((elem, index) => {
      return index !== id;
    });
    setItems(delValue);
  };

  const editItems = (id) => {
    const edit = items.find((elem, index) => {
      return id === index;
    });
    setToggle(false);
    setInput(edit);
    setEditItemIndex(id);
  };

  const editItem = () => {
    setItems(
      items.map((elem, id) => {
        if (id === editItemIndex) {
          return input;
        }
        return elem;
      })
    );
  };

  const deleteAll = () => {
    setItems([]);
  };

  return (
    <div className="App">
      <div className="items">
        <input
          type="text"
          placeholder="Add Items"
          value={input}
          onChange={(e) => setInput(e.target.value)}
        />
        {toggle ? (
          <AiOutlinePlus
            className="plus"
            title="Add Items"
            onClick={addItems}
          />
        ) : (
          <AiFillEdit
            className="plus"
            title="Edit Items"
            onClick={() => editItem()}
          />
        )}
      </div>

      <div className="showItems">
        {items.map((elem, ind) => {
          return (
            <div className="eachItems" key={ind}>
              <h3>{elem}</h3>
              <AiFillEdit title="Edit Items" onClick={() => editItems(ind)} />
              <BsFillTrashFill
                title="Del Items"
                className="del"
                onClick={() => deleteItems(ind)}
              />
            </div>
          );
        })}
      </div>

      <div className="btns">
        <button onClick={deleteAll}>Clear All</button>
      </div>
    </div>
  );
}
